<template>
  <div id="app">
    <div id="nav">
		<div id="nav-head" class="md-layout">
			<div class="md-layout-item md-size-20">
				<md-avatar><img src="./assets/logo.svg" alt=""></md-avatar>
			</div>
			<div class="md-layout-item md-size-10">
			</div>
			<div class="md-layout-item md-size-70">
				<span style="color: white; font-size: 20px;">Random<br>Wallpaper</span>
			</div>
		</div>
		<md-divider style="background-color: rgb(100,100,100); margin: 20px auto;"></md-divider>
		<div id="nav-menu" style="margin-top:100px">
			<router-link to="/">
				<md-button :class="[theme, 'md-raised']" style="width:90%">
					<md-icon>home</md-icon> Home
				</md-button>
			</router-link>
			<router-link to="/gallary">
				<md-button :class="[theme, 'md-raised']" style="width:90%">
					<md-icon>apps</md-icon> Gallary
				</md-button>
			</router-link>
			<router-link to="/setting">
				<md-button :class="[theme, 'md-raised']" style="width:90%">
					<md-icon>build</md-icon> Setting
				</md-button>
			</router-link>
		</div>
		<div id="nav-sien75">sien75.github.io</div>
    </div>
    <div id="main">
		<md-button :class="[theme, 'md-raised', 'md-dense', 'md-icon-button']" id="main-theme" @click="changeTheme">
			<md-icon>palette</md-icon>
		</md-button>
		<md-button :class="[theme, 'md-raised', 'md-dense', 'md-icon-button']" id="main-minimize"
		@click="ipcHandler({request: 'minimize'})" v-if="!ifFrame">
			<md-icon>keyboard_arrow_down</md-icon>
		</md-button>
		<md-button :class="[theme, 'md-raised', 'md-dense', 'md-icon-button']" id="main-close"
		@click="ipcHandler({request: 'close'})" v-if="!ifFrame">
			<md-icon>close</md-icon>
		</md-button>
		<keep-alive>
			<router-view :theme="theme" @ipc-event="ipcHandler" :rw-config="rwConfig" :img-maps="imgMaps" />
		</keep-alive>
    </div>
    <div id="bg">
		<img style="width: 100%; height: 100%" :src="background" alt="">
    </div>
  </div>
</template>

<script>
const { ipcRenderer } = window.require('electron');
export default {
	data () {
		return {
			theme: 'md-primary',
			allThemes: ['md-primary', 'md-accent', ''],
			curTheme: 0,
			background: null,
			ifFrame: null,
			rwConfig: {},
			imgMaps: {},
		}
	},
	methods: {
		changeTheme () {
			this.curTheme = (this.curTheme + 1) % this.allThemes.length;
			this.theme = this.allThemes[this.curTheme];
		},
		ipcHandler (arg) {
			ipcRenderer.send('info-request', arg);
		},
		getImgs (allImgs) {
			return new Promise(resolve => {
				let requireGets = allImgs.filter(img => !this.imgMaps[img]);
				if(requireGets.length == 0) {
					resolve();
					return;
				}
				ipcRenderer.send('file-request', requireGets);
				ipcRenderer.on('file-reply', (event, arg) => {
					console.log('[file-reply] ', arg);
					if(arg.state == 'finished') resolve();
					else {
						let blob = new Blob([arg.data], arg.mimeType);
						this.imgMaps[arg.name] = URL.createObjectURL(blob);
					}
				})
			})
		}
	},
	mounted () {
		this.ipcHandler({request: 'init'});
		ipcRenderer.on('info-reply', (event, arg) => {
			console.log('[info-reply]', arg);
			this.getImgs(arg ? arg.allImgs : []).then(() => {
				this.rwConfig = arg || JSON.parse(JSON.stringify(this.rwConfig));
				this.background = this.imgMaps[this.rwConfig.currentBg];
				if(this.ifFrame == null) this.ifFrame = this.rwConfig.settings.ifFrame;
			})
		})
	}
}
</script>

