<template>
	<div id="root">
		<canvas id="clock" :width="l" :height="l" :style="clockStyle"></canvas>
		<canvas id="hour" :width="l" :height="l" :style="clockStyle"></canvas>
		<canvas id="minute" :width="l" :height="l" :style="clockStyle"></canvas>
		<canvas id="second" :width="l" :height="l" :style="clockStyle"></canvas>
		<md-button :class="[theme, 'md-raised']" id="random" @click="random">
			<md-icon>gavel</md-icon>
			<span v-if="!loading"> Random</span>
		</md-button>
	</div>
</template>

<script>
//import HelloWorld from '@/components/HelloWorld.vue'
export default {
	name: 'home',
	components: {
		//HelloWorld
	},
	props: {
		theme: String,
		rwConfig: Object
	},
	watch: {
		theme () {
			setTimeout(() => {this.initClock();this.timing({immediately: true});}, 200);
		},
		rwConfig () {
			this.loading = false;
		}
	},
	data () {
		return {
			l: window.innerHeight * 0.75,
			s: 0,//second
			m: 0,//minute
			h: 0,//hour
			loading: false
		}
	},
	computed: {
		clockStyle () {
			let m_left = (window.innerWidth * 0.75 - this.l) / 2;
			let m_top = (window.innerHeight - this.l) / 2;
			return {
				left: `${m_left}px`,
				top: `${m_top}px`
			}
		},
		color () {
			if(this.theme == 'md-primary') return '#3988FA';
			else if(this.theme == 'md-accent') return '#FF5357';
			else return 'white';
		}
	},
	mounted () {
		this.initClock();
		this.timing({immediately: false});
	},
	methods: {
		initClock () {
			let cav = document.getElementById('clock');
			let ctx = cav.getContext('2d');
			let w = cav.width, h = cav.height;
			ctx.lineWidth = 5;
			ctx.strokeStyle = ctx.fillStyle = this.color;
			ctx.beginPath();
			ctx.arc(0.5 * w, 0.5 * h, 0.45 * h, 0, 2 * Math.PI);
			ctx.stroke();
			ctx.beginPath();
			ctx.arc(0.5 * w, 0.5 * h, 0.1 * h, 0, 2 * Math.PI);
			ctx.fill();
		},
		timing ({immediately}) {
			let d, s, m, o;
			let cavS = document.getElementById('second'),
				cavM = document.getElementById('minute'),
				cavH = document.getElementById('hour');
			let ctxS = cavS.getContext('2d'),
				ctxM = cavM.getContext('2d'),
				ctxH = cavH.getContext('2d');
			let w = cavS.width, h = cavS.height;
			ctxS.lineWidth = 5;
			ctxM.lineWidth = 7;
			ctxH.lineWidth = 9;
			ctxS.lineCap = ctxM.lineCap = ctxH.lineCap = 'round';
			let drawFun = () => {
				d = new Date();
				s = d.getSeconds() / 60 * 2 * Math.PI - 0.5 * Math.PI;
				m = d.getMinutes() / 60 * 2 * Math.PI - 0.5 * Math.PI;
				o = d.getHours() / 12 * 2 * Math.PI - 0.5 * Math.PI;
				ctxS.strokeStyle = ctxM.strokeStyle = ctxH.strokeStyle = this.color;
				ctxS.clearRect(0, 0, w, h);
				ctxM.clearRect(0, 0, w, h);
				ctxH.clearRect(0, 0, w, h);
				ctxS.beginPath();
				ctxM.beginPath();
				ctxH.beginPath();
				ctxS.moveTo(0.5 * w, 0.5 * h);
				ctxM.moveTo(0.5 * w, 0.5 * h);
				ctxH.moveTo(0.5 * w, 0.5 * h);
				ctxS.lineTo(0.5 * w + 0.4 * h * Math.cos(s), 0.5 * h + 0.4 * h * Math.sin(s));
				ctxM.lineTo(0.5 * w + 0.3 * h * Math.cos(m), 0.5 * h + 0.3 * h * Math.sin(m));
				ctxH.lineTo(0.5 * w + 0.2 * h * Math.cos(o), 0.5 * h + 0.2 * h * Math.sin(o));
				ctxS.stroke();
				ctxM.stroke();
				ctxH.stroke();
			}
			if(immediately) drawFun();
			else {
				let seconds = (new Date()).getSeconds();
				let t = setInterval(() => {
					if((new Date()).getSeconds != seconds) {
						clearInterval(t);
						setInterval(drawFun, 1000);
					}
				}, 20);
			}
		},
		random () {
			this.loading = true;
			this.$emit('ipc-event', {request: 'random', params: {source: this.rwConfig.settings.source}})
		}
	}
}
</script>

<style scoped>
	#root {
		width: 100%;
		height: 100%;
	}
	#clock {
		z-index: 3;
	}
	#hour {
		z-index: 4;
	}
	#minute {
		z-index: 5;
	}
	#second {
		z-index: 6;
	}
	#random {
		right: 10%;
		bottom: 10%;
		z-index: 7;
	}
	#root, #clock, #hour, #minute, #second, #random {
		position: absolute;
	}
</style>